@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:300i,400,400i,700i);
@import url(https://fonts.googleapis.com/css?family=Nunito:300);
.landing {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
}

.issue-card {
  border: 3px solid #2d00ff;
  background: white;
  padding: 32px;
  color: #2d00ff;
}

.issue-card:hover {
  cursor: pointer;
  background:#2d00ff;
  color: white;
}

.issue-card:hover {
  cursor: pointer;
  background:#2d00ff;
}

.issue-card:hover a {
  color: white;
}

.landing h2 {
 padding-bottom: 32px;
}

.issue-card a {
  color: #2d00ff;
}

.nav-bar {
  width: 100%;
  background: black;
  height: 50px;
  display: flex;
  justify-content: center;
  color: white;
  position: fixed;
  z-index: 999;
}

.nav-bar-inner {
  max-width: 85%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#propagator-title {
  color: white;
  font-size: 1.0em;
  font-style: italic;
}

.current-issue-meta {
  text-align: right;
  line-height: 0.975em;
}

.issue-title {
  font-style: italic;
  font-size: 0.875em;
}

.issue-num {
  font-size: 0.775em;
}
.zine-nav {
  width: 100%;
  background: #2d00ff;
  height: 70px;
  display: flex;
  justify-content: center;
  color: white;
  position: fixed;
  top: 50px;
  z-index: 999;
}

.zine-nav-inner {
  max-width: 85%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contributor-name {
  font-size: 0.9em;
}

.contributor-creds {
  font-size: 0.7em;
}

/* ZINE CONTROLS */
.zine-controls-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 2px solid white;
  border-radius: 50%;
  background: #2d00ff;
  color: white;
  font-size: 0.7em;
}

.zine-controls-btn:hover {
  cursor: pointer;
  background: white;
  color: #2d00ff;
}

.zine-controls-btn:disabled {
  cursor: default;
  background: #6661f8;
  color: #2d00ff;
  border: 2px solid #6661f8;
}

.zine-controls-btn-disabled,
.zine-controls-btn-disabled:hover {
  pointer-events: none;
  cursor: default;
  background: #6661f8;
  color: #2d00ff;
  border: 2px solid #6661f8;
}

.zine-ctrl-link.disabled {
  cursor: default;
}

#prev-btn {
  margin-right: 20px;
}

#next-btn {
  margin-left: 20px;
}

.page-indicator-label {
  font-size: 0.8em;
}

.zine-controls-bottom {
  display: none;
}
.zine-toc {
  height: 100%;
}

.zine-toc ul li {
  list-style-type: decimal-leading-zero;
  font-style: italic;
  font-weight: 300;
  font-size: 1em;
  line-height: 2.2em;
}

.zine-toc a {
  color: black;
}

.zine-toc a:hover {
  color: blue;
  font-weight: 400;
}

.zine-toc-container {
  width: 100%;
  display: inline-block;
  height: 100%;
  vertical-align: top;
}

.zine-toc-container {
	display: flex;
	justify-content: center;
  align-items: center;
  padding: 40px;
}

.zine-cover {
  background: black;
}
/* TODO: Probably should split this up */
.zine-submission {
  min-height: 100%;
  width: 100%;
}

.zine-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tertiary-full {
  background: #abab;
}

/* SPLIT TEMPLATE */
.split-template {
  display: flex;
  justify-content: center;
  /* margin-bottom: -4px; */
}

.split-template .split-left,
.split-template .split-right {
  width: 50%;
  justify-content: center;
  display: inline-flex;
}

.split-left.img {
  padding-top: 0px;
  padding-bottom: 0px;
}

.split-template .split-left .text,
.split-template .split-right .text {
  margin-top: 56px;
  margin-bottom: 56px;
  max-width: 70%;
  align-self: center;
}

.double-text-left {
  background: #f4f4f4;
}

.double-text-right {
  background: #efefef;
}

.text .submission-title {
  font-size: 20px;
  font-weight: 400;
  letter-spacing: -0.2pt;
}

.text {
  font-style: italic;
  font-weight: 300;
  font-size: 0.8em;
  line-height: 20px;
  /* margin: 22px 0px; */
}

.submission-title {
  font-style: normal;
}

.split-right.text-right {
  display: flex;
  justify-content: center;
}

.split-right img,
.split-left img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  align-self: flex-start;
}

/* FULL TEMPLATE */
.full-template {
  width: 100%;
  display: flex;
  justify-content: center;
}

.full-template-inner {
  padding: 60px 32px;
  width: 100%; 
}

.full-template-inner.text,
.full-template-inner.video {
  max-width: 85%;
}

.full-template-inner.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.full-template-inner.img {
  padding: 0px;
}

.full-template-inner.img .img-full {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.full-template-text {
  max-width: 80%;
}

.full-template-inner .submission-title-container {
  margin-bottom: 24px;
  text-align: center;
  font-weight: 400;
  font-style: italic;
  font-size: 20px;
}

/* https://blog.theodo.fr/2018/01/responsive-iframes-css-trick/ */
.video-embed-container {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}


/* quote */
.zine-quote {
  padding: 8px 0px;
  width: 100%;
  background-color: #e8eaff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.zine-quote-inner {
  width: 70%;
  text-align: center;
}

.zine-quote-inner p {
  text-transform: uppercase;
  font-size: 1.4em;
  line-height: 1.7em;
  font-style: italic;
  font-weight: 300;
}

/* secondary */
.custom-site {
  width: 90%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.custom-site a {
  color: #007bff;
}

.custom-site a:hover {
  text-decoration: underline;
}

#sleep-to-learn {
  font-family: "Nunito", sans-serif;
  line-height: 1.5;
  color: white;
  border: 5px solid blue;
  width: 100%;
  position: relative;
  overflow: hidden;
  max-height: 700px;
}

#sleep-to-learn h1, 
#sleep-to-learn h2 {
  text-align: center;
  text-shadow: #000 0 0 10px;
}

#sleep-to-learn h1 {
  font-size: 4em;
  margin-top: 25%;
  margin-bottom: 0px;
}

#sleep-to-learn h2 {
  font-size: 2em;
}

#sleep-to-learn p {
  font-size: 1.2em;
}

.fullscreen-bg__video {
  width: 100%;
  height: 100%;
}

.col-12 {
  margin-bottom: -12px;
}

.sleep-container {
  height: 100%;
  display: flex;
  overflow: auto;
  position: absolute;
  width: 100%;
  padding: 0 5%;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.sleep-container img {
  max-width: 100%;
}

.essay {
  background-color: rgba(0, 0, 0, 0.75);
  padding: 28px;
  display: flex;
  flex-direction: column;
  margin-left: 12%;
  margin-right: 12%;
}

.round-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.round-bottom {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 15px;
}
@media screen and (max-width:850px) {
  /* TOC */
  .zine-cover,
  .zine-toc-container {
    width: 100%
  }

  .zine-toc ul li {
    font-size: 0.9em;
    line-height: 2.2em;
  }

  .zine-content {
    padding-top: 100px;
  }

  .zine-submission {
    padding-bottom: 50px;
  }

  /* Zine Nav */
  .zine-controls {
    display: none;
  }

  .zine-nav {
    height: 50px;
    line-height: 1em;
  }

  .contributor-name {
    font-size: 0.8em;
  }
  
  .contributor-creds {
    font-size: 0.6em;
  }
 
  .zine-controls-bottom {
    display: block;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #2d00ff;
    position: fixed;
  }

  .disabled {
    pointer-events: none;
  }

  .zine-controls-btn {
    border: 0;
    width: 38px;
    height: 38px;
    background: white;
  }

  .zine-controls-btn-disabled {
    background: #545eff;
  }

  .zine-controls-btn:hover {
    background: white;
  }

  a.zine-ctrl-link {
    max-height: 38px;
  }

  .zine-controls-icon {
    width: 11px;
  }

  .zine-controls-icon.icon-left {
    margin-right: 2px;
  }
  
  .zine-controls-icon.icon-right {
    margin-left: 2px;
  }
  
  .zine-controls-btn:disabled {
    cursor: default;
    background: #2d00ff;
    color: white;
    border: 0;
  }

  #nav-btn-img {
    height: 30px;
    width: 30px;
  }

  .page-indicator-label {
    color: white;
  }

  /* SplitTemplates */
  .split-template {
    display: block;
  }

  .split-template .split-left,
  .split-template .split-right {
    width: 100%;
  }

  .split-template .split-left .text,
  .split-template .split-right .text {
    margin-top: 56px;
    margin-bottom: 56px;
    max-width: 80%;
  }
  
  /* FullTemplate */
  .full-template-inner {
    padding: 56px 0px;
  }

  .full-template-inner.text {
    max-width: 100%;
  }

  /* Sleep To Learn */
  #sleep-to-learn h1 {
    font-size: 26px;
  }

  #sleep-to-learn h2 {
    font-size: 18px;
  }

  #sleep-to-learn p {
    font-size: 0.8em;
  }

  #sleep-to-learn .essay {
    padding: 16px;
    margin-left: 8%;
	  margin-right: 8%;
  }
}

/* COMPONENT CSS */

* {
  box-sizing: border-box;
}

*:focus {
  outline: none !important;
}

a, a:visited, a:active {
  text-decoration: none;
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
}

#root, .App, .zine {
  height: 100%;
}

.zine-content {
  padding-top: 118px;
  height: 100%;
}
